.hamburger-nav {
    width: 22px;
    height: 19px;
    position: relative;
    -webkit-transition: .5s ease-in-out;
    transition: .5s ease-in-out;
    cursor: pointer;
}

.hamburger-nav, .hamburger-nav span {
    -webkit-transform: rotate(0);
    transform: rotate(0);
}

.hamburger-nav span:first-child {
    top: 0;
}
.hamburger-nav span {
    display: block;
    position: absolute;
    height: 3px;
    width: 100%;
    background: #fff;
    border-radius: 9px;
    opacity: 1;
    right: 0;
    margin-left: auto;
    -webkit-transition: .25s ease-in-out;
    transition: .25s ease-in-out;
}

.hamburger-nav, .hamburger-nav span {
    -webkit-transform: rotate(0);
    transform: rotate(0);
}

.hamburger-nav span:nth-child(2), .hamburger-nav span:nth-child(3) {
    top: 7px;
    width: 80%;
}

.hamburger-nav span:nth-child(3) {
    top: 14px;
    width: 60%;
}

.hamburger-nav.open span {
    width: 100%
}

.hamburger-nav.open span:first-child,.hamburger-nav.open span:nth-child(4) {
    top: 7px;
    width: 0;
    right: 50%;
}

.hamburger-nav.open span:nth-child(2) {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg)
}

.hamburger-nav.open span:nth-child(3) {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg)
}


